import React, {useEffect, useRef, useState} from "react";
import Modal from "@material-ui/core/Modal";
import styles2 from "../../css/COLUMN.module.css";
import ElemDeleteControl from "../ElemDeleteControlWrapper";
import ReportModal from "./ReportModal";
import { makeStyles } from "@material-ui/core";
import QuickTools from '../../../ReusableComponents/QuickTools/QuickTools';
import { getSuggestedId } from "../../../components/IFormDesignerLayout/Logic/UserIdLogic";
import {useSelector} from "../../../Redux"
import { handleDisplayProperty } from "../../../components/IFormDesignerLayout/IFormDesignerLogic";
import { GetState, GetStateFromData, useElementWidth } from "../../../components/CustomHooks/CustomHooks";
import { useTranslation } from "react-i18next";
import secureLocalStorage from "react-secure-storage";

const useStyles = makeStyles({
  rootdiv: {
    // border: "1px solid #243ED0",
    // border: "1px solid grey",
    // maxWidth: "410px",
    width: "100%",
    minHeight: "150px",
    height: (style) =>
      style.Height !== "" ? `${style.Height}px` : "150px",
    // display: style=> style.Visible ?  "flex" : "none" || "flex",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    // margin: "10px",
  }
});

const Report = (props) => {

const isSubformInPage = props?.isSubformInPage || false;
  const {t}=useTranslation();
  const [open, setOpen] = React.useState(false);
  const iFrameSrcRef = useRef("")

  const handleOpen = () => {
    let iframe = document.getElementById(suggestedId);
    iFrameSrcRef.current = iframe.src
    setOpen(true);
  };

  const handleClose = (reportname,initiator) => {
     setOpen(false);
     let iframe = document.getElementById(suggestedId);
     if(!props.data.selectId){
      iframe.removeAttribute("src")
     }
     if(initiator === "cancel"){
      iframe.src = iFrameSrcRef.current
     }
  };

  const contexts = GetStateFromData(props.containerId);

  const {controlSelected,tempCanWidth} = GetState("controlSelected","tempCanWidth");
  const { IdPrecedence, quickToolsClose ,deleteControl, formState2, openQuickToolFor, device,formMataData,builderMode,appname,isMicroSerciceChecks,cabinetName }=contexts.contexts;
  const conditionalQuickToolOpening =
    props.id === openQuickToolFor ? true : false

  const actualControlId = props.data.id;
  let mappingId = props.data["style"] && props.data["style"]["MappingId"];
  let userDefinedId = props.data["style"] && props.data["style"]["CustomId"];
 
  let suggestedId = getSuggestedId(actualControlId, mappingId, userDefinedId, IdPrecedence);

  const [selectedReportId, setSelectedReportId] = React.useState();
 /* const [sessionIdConnection, setSessionIdConnection] = React.useState();
  
  const [data, setData] = React.useState([]);
  const [enableNext, setEnableNext] = React.useState();
  const [enablePrev, setEnablePrev] = React.useState();
  
  const [options,  setOptions] = React.useState([]);*/

  const [mobileDisplay, setMobileDisplay] = useState(true);
  const [tabDisplay, setTabDisplay] = useState(true);
	
	



    

  /*useEffect(()=>{
     
      if(!window?.loadMicroFrontend){
        window.loadIntegrator_bam();
      }
      if(props.data.selectId!==undefined){
        setSelectedReportId(props.data.selectId);  
      }
  },[])*/
  console.log("Report3", props)
  useEffect(() => {
   console.log("Report", props?.containerId)
    if (props.data.selectId !== undefined) {
      setSelectedReportId(props.data.selectId);
      if (!window?.loadMicroFrontend) {
      

        window.loadIntegrator_bam();
      }
    }
  }, []);
  
  var globalTheme=formState2.theme;
  
  const [openQuick, setOpenQuick] =  useState(conditionalQuickToolOpening);

  const style = props.data.style;

  const classes = useStyles(style);

 /* useEffect(()=>{
    if(!window?.loadMicroFrontend && props.data.selectId!==undefined){
      window.loadIntegrator_bam();
      
    }
  },[])*/

  const startMobile = useRef();
  const endMobile= useRef();
  const startTab= useRef();
  const endTab= useRef();
  // let canvasWidth=useElementWidth(null, props?.previewmode);

  useEffect(() => {
      if (formState2 && formState2.style) {
          startMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["StartValue"]);
          endMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["EndValue"]);
          startTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["StartValue"]);
          endTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["EndValue"]);
      }
  },[formState2.style?.["Breakpoint"], formState2.style?.["Breakpoint"]?.["Mobile"], formState2.style?.["Breakpoint"]?.["Tab"]]); 

  useEffect(() => {
    if(props?.previewmode)
    handleDisplayProperty(props, device, setMobileDisplay, setTabDisplay, startMobile.current, endMobile.current, startTab.current, endTab.current)
  },[props?.data?.style?.["MobileVisible"], props?.data?.style?.["TabsVisible"], device, endMobile, endTab, startMobile, startTab,tempCanWidth]);


  var padding_left = props.data.style && props.data.style["SpacingLeft"];
  if(padding_left !==undefined && padding_left !==null && padding_left !==''){

  } else {
      padding_left="0px";
  }

  var padding_right = props.data.style && props.data.style["SpacingRight"];
  if(padding_right !==undefined && padding_right !==null && padding_right !==''){

  } else {
      padding_right="0px";
  }

  var padding_top = props.data.style && props.data.style["SpacingTop"];
  if(padding_top !==undefined && padding_top !==null && padding_top !==''){

  } else {
      padding_top="0px";
  }

  var padding_bottom = props.data.style && props.data.style["SpacingBottom"];
  if(padding_bottom !==undefined && padding_bottom !==null && padding_bottom !==''){

  } else {
      padding_bottom="0px";
  }


  var inputStylePadding={  
      paddingLeft : padding_left,
      paddingRight : padding_right,
      paddingTop : padding_top,
      paddingBottom : padding_bottom,
      display:"block",
      width:"100%",
      height:"100%"
  }
  function callBack_func() {
    console.log("callBack_func");
  }

  function loadReport() {
    const language = window?.depsatkur_configs?.selectedLanguage;

    // const secureAutherization = secureLocalStorage.getItem("Authorization");
    // const secureAutherization1 = secureLocalStorage.getItem("launchpadKey");
    // const jwtToken = secureAutherization || JSON.parse(secureAutherization1)?.token || undefined;
    const jwtToken =
    JSON.parse(secureLocalStorage.getItem(window.isPortalLive ?  `launchpadKey_${window.applicationName}` || "launchpadKey" : "launchpadKey"))?.token ||
  secureLocalStorage.getItem("Authorization") ||
  undefined;

   console.log("Report5")
   if(isMicroSerciceChecks.IsMicroserviceMode === "Y"){
    var propsData = {
      Module: "BAM",
      token: isMicroSerciceChecks?.MicroAuthToken || jwtToken,
      Component: "ReportViewer",
      InFrame: true,
      ContainerId: props.id,
      locale: "en_US",
      Callback: callBack_func,
      PathParams: { ReportId: selectedReportId},
      "Accept-Language": language,
      "TenantId": cabinetName,
      "UserIndex":isMicroSerciceChecks?.MicroAuthParam,
      "Authorization":isMicroSerciceChecks?.MicroAuthToken,
      "is_msmode":true,
      "UserId":isMicroSerciceChecks?.MicroAuthUser
    };
   }else{
    var propsData = {
      Module: "BAM",
      token: jwtToken,
      Component: "ReportViewer",
      InFrame: true,
      ContainerId: props.id,
      locale: "en_US",
      Callback: callBack_func,
      PathParams: { ReportId: selectedReportId},
      // "Accept-Language": language,
      // "TenantId": cabinetName,
      // "UserIndex":isMicroSerciceChecks?.MicroAuthParam,
      // "Authorization":jwtToken,
      // "is_msmode":true,
      // "UserId":isMicroSerciceChecks?.MicroAuthUser
    };
   }
    
    window.loadMicroFrontend_formBuilder(propsData);
  }

  useEffect(() => {
    console.log("Report1", props?.containerId, typeof props?.containerId, selectedReportId)
    if(window.location.href.indexOf("pmWebPreview") > -1){
    setTimeout(() => {
      if (window.MicroAppsMap && Object.keys(window.MicroAppsMap).length>0 && selectedReportId) {
        loadReport();
        
      }
    }, 1000)
  }
  else{
    if (window.MicroAppsMap && Object.keys(window.MicroAppsMap).length>0 && selectedReportId) {
      loadReport();
      
    }
  }
    
  }, [selectedReportId,window.MicroAppsMap]);

//   useEffect(() => {
// if(window?.newMicroFrontendData !== undefined){
//   console.log("Report4", props)
// }
//   }, [window?.newMicroFrontendData])
if(props.previewmode ){
  return (
    <>
        {style && style.Visible !== "false" && 
      <div id={suggestedId+'_group'}
        // style={mobileDisplay === false || tabDisplay === false ? {display:"none"} : {display:"block", width: "100%", height: "100%"}}
        style={mobileDisplay === false || tabDisplay === false ? {display:"none"} : inputStylePadding}
        // style={{ width: "100%", height: "100%" }}
        key={props.id}
        // id={props.id}
        frameindex={props.data.frameIndex}
        elementindex={props.data.elementIndex}
        control_id={props.id}
        className={`${styles2.formStyle} `}
        elem_type="report"
        tabIndex="1"

        // draggable={true} onDragStart={props.handleReorderStart} onDrop={(ev)=>props.handleReorderDrop(ev,props.data.elem_type)}
      >
        <div elem_type="report" control_id={props.id}>
          {style && style.Visible !== "false" && (
            <>
            <iframe
              className={classes.rootdiv}
              elem_type="report"
              control_id={props.id}
              id={suggestedId}
            >              
            </iframe>
            <div elem_type="report" control_id={props.id}>
               {!formMataData && !(appname && builderMode==="application")  && <button
                  type="button"
                  style={{
                    border: "none",
                    background: "none",
                    cursor: "pointer",
                    color: "#0072C6",
                  }}
                  onClick={handleOpen}
                >
                  + {t("Add Report")}
                </button>}
                <Modal open={open} onClose={handleClose}>
                  
                  <ReportModal closeModal={handleClose}  data={props.data} 
                    containerId={props?.containerId}
                  />
                </Modal>
              </div>
              </>
          )}
        </div>
      </div>
          }
        
    </>
  );
}
else {
  return (
  <div id={suggestedId+'_group'}
  onClick={isSubformInPage ? {} : (e) => props.handleSelected(e, props.id, "n")}
  onKeyDown={(e) => props.handleSelected(e)}
  
  // style={mobileDisplay === false || tabDisplay === false ? {display:"none"} : {display:"block", width: "100%", height: "100%"}}
  style={mobileDisplay === false || tabDisplay === false ? {display:"none"} : inputStylePadding}
  // style={{ width: "100%", height: "100%" }}
  key={props.id}
  // id={props.id}
  frameindex={props.data.frameIndex}
  elementindex={props.data.elementIndex}
  control_id={props.id}
  className={`${styles2.formStyle} `}
  elem_type="report"
  tabIndex="1"

  // draggable={true} onDragStart={props.handleReorderStart} onDrop={(ev)=>props.handleReorderDrop(ev,props.data.elem_type)}
>
  <div elem_type="report" control_id={props.id}>
      <>
      <iframe
        className={classes.rootdiv}
        elem_type="report"
        control_id={props.id}
        id={suggestedId}
      >              
      </iframe>
      <div elem_type="report" control_id={props.id}>
         {!formMataData && !(appname && builderMode==="application")  && <button
            type="button"
            style={{
              border: "none",
              background: "none",
              cursor: "pointer",
              color: "#0072C6",
            }}
            onClick={handleOpen}
          >
            + {t("Add Report")}
          </button>}
          <Modal open={open} onClose={handleClose}>
            
            <ReportModal closeModal={handleClose}  data={props.data} 
              containerId={props?.containerId}
            />
          </Modal>
        </div>
        </>
  </div>
  {controlSelected?.id === props?.id &&
  <ElemDeleteControl 
    isSubformInPage={isSubformInPage}
    containerId={props.containerId}
    id={props.data.id} 
    data={props.data}
    deleteControl={deleteControl} 
    elemType={props.data?.elem_type}
    setDragging={props.setDragging}
    handleReorderStart={props.handleReorderStart}
    handleReorderDrop={props.handleReorderDrop}
    dragId={props.dragId}
    dragType={'column'} 
  /> 
}
  {
    window?.openQuickTools && openQuick &&  
    <QuickTools containerId={props.containerId} selectedControl={props.data} open={openQuick} setOpen={setOpenQuick} />
  }
</div>)

}
  
};

export default Report;
